define(function() {
    'use strict';
    var names = {
        display: 'services/display',
        vector: 'services/display/vector',
        trafficFlow: '',
        traffic: '',
        search: '',
        routing: ''
    };
    /**
     * Immutable object with the names of the different API keys.
     */
    return Object.freeze(names);
});



